import gql from 'graphql-tag'

const DELETE_GUIDE_PENDING = gql`mutation deletePendingGuide($guide: String!){
    deletePendingGuide(guide: $guide){
        success
    }
}`

const CREATE_GUIDE_PENDING = gql`mutation createPendingGuide($guide: PendingGuidesInput!){
    createPendingGuide(guide: $guide){
        success
        guide{
            guideNumber
            created
            store{
                name
            }
        }
    }
}`

export {
    DELETE_GUIDE_PENDING,
    CREATE_GUIDE_PENDING
}