import gql from 'graphql-tag'

const GET_PENDING_GUIDES = gql`query getPendingGuides($guideNumber: String, $createdDateGt: DateTime, $createdDateLt: DateTime, $createdDateGte: DateTime, $createdDateLte: DateTime, $after: String){
    pendingGuides(guideNumber_Icontains: $guideNumber, created_Date_Gt: $createdDateGt, created_Date_Lt: $createdDateLt, created_Date_Gte: $createdDateGte, created_Date_Lte: $createdDateLte, after: $after){
        pageInfo{
            endCursor
            hasNextPage
        }
        edges{
            node{
                created
                guideNumber
                store{
                    name
                }
            }
        }
    }
}`

export {
    GET_PENDING_GUIDES
}