<template>
  <layout>
    <page-header :title="title"></page-header>
    <b-card v-if="can('add_pendingguides')">
      <b-card-text>
        <b-form-group
        >
          <template v-if="loadingCreate">
            <b-skeleton type="input"></b-skeleton>
          </template>
          <template v-else>
            <b-form-input
              id="input-1"
              v-model="guideNumberCreate"
              :state="state($v.guideNumberCreate)"
              trim
              placeholder="Escanee aquí el número de guía"
              @keyup.enter="validate"
              :disabled="loadingCreate"
            ></b-form-input>
          </template>
        </b-form-group>
      </b-card-text>
    </b-card>
    <b-card>
      <b-card-text>
        <b-row>
          <b-col md="4">
            <b-form-group class="mb-2">
              <b-form-datepicker
                v-model="startDate"
                placeholder="Fecha Inicial"
                :max="maxDate"
              >
              </b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="4">
            <b-form-group class="mb-2">
              <b-form-datepicker
                v-model="endDate"
                placeholder="Fecha Final"
                :max="maxDate"
                :min="minEndDate"
              >
              </b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="1">
            <b-button
              :disabled="startDate !== null && endDate !== null ? false : true"
              @click="getPendingGuides(true)"
              variant="success"
            >
              Enviar
            </b-button>
          </b-col>
          <b-col md="1">
            <b-button
              v-show="startDate && endDate"
              @click="clearDates()"
              variant="info"
            >
              Limpiar
            </b-button>
          </b-col>
        </b-row>
        <b-row>
          <b-col lg="4" md="4">
            <b-form @submit.prevent class="mb-3">
              <b-form-group>
                <b-input-group>
                  <b-form-input
                    id="input-1"
                    v-model="searchGuide"
                    type="text"
                    placeholder="Buscar por número de guía"
                    required
                  ></b-form-input>
                  <b-input-group-append>
                    <b-button
                      type="submit"
                      :disabled="!searchGuide"
                      @click="getPendingGuides(true)"
                      variant="warning"
                      >Buscar</b-button
                    >
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-form>
          </b-col>
        </b-row>
        <template v-if="loadingGuides">
          <b-skeleton-table
            :rows="10"
            :columns="2"
            :table-props="{ bordered: true, striped: true }"
          ></b-skeleton-table>
        </template>
        <template v-else>
          <div class="table-responsive">
            <b-table
              striped
              hover
              :items="pendingGuidesTable"
              :fields="fields"
              responsive="sm"
            >
              <template v-slot:cell(actions)="{ item }" v-if="can('delete_pendingguides')">
                <b-button
                  variant="danger"
                  size="sm"
                  @click="modalQuestionDelete(item.guideNumber)"
                >
                  <i class="mdi mdi-delete"></i>
                </b-button>
              </template>
            </b-table>
          </div>
        </template>
      </b-card-text>
      <div class="text-center mt-2" v-if="hasNextPage">
        <b-button block variant="primary" :disabled="loadingMore">
          <template v-if="loadingMore">
            <b-spinner small type="grow"></b-spinner>
            Cargando...
          </template>
          <template v-else> Ver más </template>
        </b-button>
      </div>
    </b-card>
  </layout>
</template>
<script>
import Layout from "@/router/layouts/main";
import PageHeader from "@/components/page-header";
import permissionMixin from "@/mixins/permissionMixin";
import { GET_PENDING_GUIDES } from "@/api/graphql/query/pendingGuide";
import { DELETE_GUIDE_PENDING, CREATE_GUIDE_PENDING } from "@/api/graphql/mutations/pendingGuide";
import { toast } from "@/utils/alerts";
import { formatDate } from "@/utils/general";
import Swal from "sweetalert2";
import { required } from "vuelidate/lib/validators";

export default {
  name: "PendingGuide",
  mixins: [permissionMixin],
  components: {
    PageHeader,
    Layout,
  },
  data() {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    return {
      title: "Guías pendientes",
      loadingGuides: true,
      hasNextPage: false,
      loadingMore: false,
      pendingGuidesTable: [],
      fields: [
        {
          key: "guideNumber",
          label: "Número de guía",
        },
        {
          key: "store",
          label: "Tienda",
        },
        {
          key: "created",
          label: "Fecha de creación",
        },
        {
          key: "actions",
          label: "Acciones",
        },
      ],
      guidesDetail: [],
      guideLocalSelect: null,
      endCursor: null,
      searchGuide: null,
      startDate: null,
      endDate: null,
      maxDate: today,
      minEndDate: null,
      loadingDelete: false,
      guideNumberCreate: null,
      loadingCreate: false
    };
  },
  validations: {
    guideNumberCreate: { required }
  },
  watch: {
    searchGuide: function (newValue) {
      if (newValue === "") {
        this.pendingGuidesTable = [];
        this.endCursor = null;
        this.getPendingGuides();
        this.loadingGuides = true;
      }
    },
    startDate: function (newValue) {
      if (this.startDate > this.endDate) {
        this.endDate = this.startDate;
      }
      const startDate = new Date(this.startDate);
      const minDate = new Date(
        startDate.getFullYear(),
        startDate.getMonth(),
        startDate.getDate() + 1
      );
      this.minEndDate = minDate;
    },
  },
  methods: {
    state(item) {
      const { $dirty, $error } = item;
      return $dirty ? !$error : null;
    },
    validate(){
      this.$v.guideNumberCreate.$touch()
      const isValid = !this.$v.guideNumberCreate.$invalid
      if(isValid === true){
        this.modalQuestionCreate()
      }
      return isValid
    },
    clearDates() {
      this.startDate = null;
      this.endDate = null;
      this.pendingGuidesTable = [];
      this.endCursor = null;
      this.hasNextPage = null;
      this.loadingGuides = true;
      this.getPendingGuides();
    },
    async getPendingGuides(isSearch = false) {
      if(isSearch === true){
        this.loadingGuides = true
        this.pendingGuidesTable = []
        this.endCursor = null
      }
      this.loadingMore = true
      try {
        const response = await this.$apollo.query({
          query: GET_PENDING_GUIDES,
          variables: {
            after: this.endCursor,
            guideNumber: this.searchGuide,
            createdGte:
              this.startDate !== null ? new Date(this.startDate) : null,
            createdLte: this.endDate !== null ? new Date(this.endDate) : null,
          },
        });
        const { pendingGuides } = response.data;
        this.loadingMore = false
        if (pendingGuides === null) {
          this.loadingGuides = false;
          return toast("warning", "No se encontró información");
        }
        this.formatTable(pendingGuides.edges);
        this.loadingGuides = false;
      } catch (error) {
        this.loadingMore = false
        this.loadingGuides = false;
        return toast("warning", "No se pudo obtener la información");
      }
    },
    formatTable(data) {
      for (let item of data) {
        const temp = {
          guideNumber: item.node.guideNumber,
          store: item.node.store.name,
          created: formatDate(item.node.created),
        };
        this.pendingGuidesTable.push(temp);
      }
    },
    async deletePendingGuide(guideNumber) {
      this.loadingDelete = true;
      try {
        const response = await this.$apollo.mutate({
          mutation: DELETE_GUIDE_PENDING,
          variables: {
            guide: guideNumber,
          },
        });
        const { deletePendingGuide } = response.data;
        if (deletePendingGuide === null) {
          this.loadingDelete = false;
          return toast("error", "Fallido");
        }
        if (deletePendingGuide.success === false) {
          this.loadingDelete = false;
          return toast("error", "Fallido");
        }
        this.loadingDelete = false;
        this.loadingGuides = true;
        this.pendingGuidesTable = [];
        this.endCursor = null;
        this.getPendingGuides();
        return toast("success", "Guía eliminada exitosamente");
      } catch (error) {
        this.loadingDelete = false;
        return toast("error", "No se pudo eliminar la guía");
      }
    },
    modalQuestionDelete(guideNumber) {
      Swal.fire({
        title: "¿Estas seguro?",
        text: `Eliminar guía Nº: "${guideNumber}"`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "Si, continuar",
        confirmButtonColor: "#bb2d3b",
        cancelButtonText: "No, cancelar",
        cancelButtonColor: "",
        showLoaderOnConfirm: true,
        preConfirm: () => {
          return this.deletePendingGuide(guideNumber);
        },
        allowOutsideClick: false,
        allowEscapeKey: false,
      });
    },
    async createPendingGuide(){
      this.loadingCreate = true
      try {
        const response = await this.$apollo.mutate({
          mutation: CREATE_GUIDE_PENDING,
          variables: {
            guide: {
              guideNumber: this.guideNumberCreate
            }
          }
        })
        const { createPendingGuide } = response.data
        if(createPendingGuide === null){
          this.loadingCreate = false
          return toast('error', 'No se pudo crear la guía')
        }
        if(createPendingGuide.success === false){
          this.loadingCreate = false
          return toast('error', 'No se pudo crear la guía')
        }
        this.loadingCreate = false
        this.addNewGuideToTable(createPendingGuide.guide)
        this.guideNumberCreate = null
        return toast('success', 'Creado con exito.')
      } catch (error) {
        console.log(error)
        this.loadingCreate = false
        return toast('error', 'No se pudo crear la guía')
        
      }
    },
    addNewGuideToTable(guide){
      const temp = {
        guideNumber: guide.guideNumber,
        store: guide.store.name,
        created: guide.created
      }
      this.pendingGuidesTable.push(temp)
    },
    modalQuestionCreate() {
      Swal.fire({
        title: "¿Estas seguro?",
        text: `Crear guía con Nº: "${this.guideNumberCreate}"`,
        icon: "question",
        showCancelButton: true,
        confirmButtonText: "Si, continuar",
        confirmButtonColor: "#bb2d3b",
        cancelButtonText: "No, cancelar",
        cancelButtonColor: "",
        showLoaderOnConfirm: true,
        preConfirm: () => {
          return this.createPendingGuide();
        },
        allowOutsideClick: false,
        allowEscapeKey: false,
      });
    }
  },
  beforeMount() {
    this.getPendingGuides();
  },
};
</script>
<style scoped>
.text_right {
  text-align: right;
}
</style>